(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('RegisterController', RegisterController);

  /** @ngInject */
  function RegisterController($log, $state, fieldName, defaultAvatarPath, vmsClient, vmsErrorMessage, authPrinciple, $scope, cities, vmsLocalStorage) {
    var vm = this;
    vm.cities = cities;

    $log.log('RegisterController');

    vm.showAvatar = defaultAvatarPath;

    vm.datePicker = {
      mode: 'year',
      dateOptions: {
        datepickerMode: "'year'",
        minMode: "'year'",
        minDate: "minDate",
        showWeeks: "false"
      },
      status: {
        opened: false
      },
      format: 'yyyy'
    }

    vm.register = function() {
      $log.debug('=== register ===');

      $log.debug(vm.volunteer);

      if (vm.showAvatar != defaultAvatarPath) {
        vm.volunteer.avatar = vm.showAvatar;
      }

      var onSuccess = function(response) {

        vmsLocalStorage.setUsername(vm.volunteer.username);
        vmsLocalStorage.setJwt(response.data.auth_access_token);
        vmsLocalStorage.setRole('volunteer');

        $state.go('registerSuccess', {
          last_name: vm.volunteer.last_name,
          email: vm.volunteer.email
        });
      };
      var onFailure = function(response) {
        $log.debug('error');
        $log.debug(response);

        if (response.status == 422) {
          $log.error(response.data);

          var errors = response.data.errors;
          vm.errorMsg = vmsErrorMessage.getErrorMsg(errors);
        }
      };

      vmsClient.register(vm.volunteer).then(onSuccess).catch(onFailure);
    };

    vm.selectAvatar = function(event, fileReader, file, fileList, fileObjects, object) {
      var base64Image = "data:" + object.filetype + ';base64,' + object.base64
      vm.showAvatar = base64Image;
    };

    vm.openDatePicker = function($event) {
      $log.debug("click openDatePicker");
      $event.preventDefault();
      $event.stopPropagation();
      vm.datePicker.status.opened = true;
    }
  }
})();

(function() {
  'use strict';

  angular
    .module('vmsFrontend')
    .controller('ManageProjectMemberController', ManageProjectMemberController);

  /** @ngInject */
  function ManageProjectMemberController($log, $stateParams, vmsClient) {
    var vm = this;
    // vm.memberCustomFieldDataList = mapping(membersData);

    angular.element(document).ready(getAllMembersCustomFieldData);

    function getAllMembersCustomFieldData() {
      var onSuccess = function(response) {
        $log.debug(response.data);
        vm.rawList = response.data;
        vm.memberCustomFieldDataList = mapping(response.data);
      };
      var onFailure = function(response) {
        $log.debug(response);
      };

      vmsClient.getAllMembersCustomFieldData($stateParams.projectId)
        .then(onSuccess)
        .catch(onFailure);
    }

    function mapping(list) {
      $log.debug('### mapping() ###');
      var result = [];

      angular.forEach(list.data, function(item) {
        var memberId = item.relationships.member.data.id;
        $log.debug(item);

        if (!angular.isDefined(result[memberId])) {
          result[memberId] = [];
        }

        result[memberId].push(item);
      });

      $log.debug('=== result ===');
      $log.debug(result);

      return result;
    }

    vm.getIncludedMember = function(id) {
      var result = false;

      $log.debug('=== getIncludedMember ===');
      $log.debug(id);

      angular.forEach(vm.rawList.included, function(value) {
        if (value.type == 'members' && value.id == id) {
          result = value.attributes;
        }
      });

      return result;
    };

    vm.getIncludedCustomField = function(id) {
      var result = false;

      angular.forEach(vm.rawList.included, function(value) {
        if (value.type == 'project_custom_fields' && value.id == id) {
          result = value.attributes;
        }
      });

      return result;
    };
  }
})();

